.App {
  text-align: center;
  height: 500px;
  background-color: white;
}

.App-logo {
  height: 40vmin;
}
.card {
  border: #282c34;
  border-width: 3px;
  box-shadow: 0 4px 8px 0 #282c34;
  width: 200px;
  height: 100px;
  

}
.container {
  display: inline-block;
  padding: 20px;
  background-color: aliceblue;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
